import React from 'react'
import Header from "./Header";
import ContactCard from './ContactCard';
import Footer from "./Footer"

const cc = [
  {
    name: "Mr. Wiskerson",
    imgUrl:
      "https://cdn.pixabay.com/photo/2017/02/20/18/03/cat-2083492__480.jpg",
    phone: "09009090",
    email: "mrwiskerson@g.com",
  },
  {
    name: "Mr. Fluffykins",
    imgUrl:
      "https://cdn.pixabay.com/photo/2017/02/20/18/03/cat-2083492__480.jpg",
    phone: "080800",
    email: "mrfluffykinsn@g.com",
  },
 ];

 const AllContacts = () => {
  return cc.map((item) => {
    return (
      <ContactCard
        contact={{
          key:item,
          name: item.name,
          imgUrl: item.imgUrl,
          phone: item.phone,
          email: item.email,
        }}
      />
    );
  });
 };
 

function App() {
  return (
    <div>
    <Header />
    <AllContacts />
    <Footer />
  </div>
  )
}

export default App


